body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  background: #333
}

html {
  font-size: 62.5%;
}
.App {
  min-width: 300px;
  width: 35%;
  min-height: 95vh;
  margin: 2vh auto;
  border: 1px solid #61DAF7;

  background: #1F1F1F;
  text-align: center;
  color: #DDDDDD;
  word-break: break-all;
}

@media (max-width: 1500px) {
  .App {
    width: 45%;
  }
}

@media (max-width: 1050px) {
  .App {
    width: 57%;
  }
}

@media (max-width: 750px) {
  .App {
    margin: .3rem auto;
    width: 98vw;
  }
}
.header {
   margin: 0 auto;
}

.header__title {
   border-bottom: 1px solid #61DAF7;
   font-size: 4rem;
   letter-spacing: .2rem;
   line-height: 15rem;
}

.header__time {
   margin: .7rem;
   font-size: 20px;
}

.header__form {
   display: -webkit-flex;
   display: flex;
   -webkit-flex-wrap: wrap;
           flex-wrap: wrap;
   -webkit-justify-content: space-between;
           justify-content: space-between;
   -webkit-align-items: flex-start;
           align-items: flex-start;
   width: 70%;
   padding: 0 2rem 0 2rem;
   margin: 2rem auto 0;
}

.header__input {
   -webkit-flex-grow: 1;
           flex-grow: 1;
   height: 4rem;
   padding: 0 1.5rem;
   margin-bottom: 2.5rem;
   border: none;
   background: #DDD;
   font-size: 1.6rem;
}

.header__icon {
   margin: 0 .7rem;
   font-size: 3rem;
   line-height: 4rem;
   cursor: pointer;
   transition: .2s;
}

.header__icon:hover {
   color: #61DAF7;
   -webkit-transform: scale(1.2);
           transform: scale(1.2)
}

.header__warning-info {
   margin: 2rem auto;
   font-size: 1.8rem;
}

@media (max-width: 1050px) {
   .header__form {
      -webkit-justify-content: space-evenly;
              justify-content: space-evenly;
   }

   .header__input {
      -webkit-flex-basis: 100%;
              flex-basis: 100%
   }

   .header__icon {
      margin: .7rem;
   }
}

@media (max-width: 750px) {
   .header__title {
      font-size: 3.5rem;
      letter-spacing: .3rem;
      line-height: 10rem;
   }
}
.doneTasks {
   border-top: 1px solid #61DAF7;
}

.doneTasks__title {
   margin: 2.5rem auto;
   font-size: 3.5rem;
}

.doneTasks__list {
   padding: 0;
   list-style: none;
}

.doneTasks__item {
   width: 60%;
   padding-bottom: 1.2rem;
   margin: 0 auto 2rem auto;
   border-bottom: 1px dashed #61DAF7;
   font-size: 2.5rem;
   letter-spacing: .2rem;
}

@media (max-width: 750px) {
   .doneTasks__title {
      font-size: 3rem;
   }

   .doneTasks__item {
      width: 80%;
   }
}

@media (max-width: 600px) {
   .doneTasks__title {
      font-size: 2.5rem;
   }

   .doneTasks__item {
      width: 90%;
   }
}
.searchedTasks__title {
   margin: 1.5rem auto;
   font-size: 3.5rem;
}

.searchedTasks__list {
   padding: 0;
   list-style: none;
}

.searchedTasks__item {
   width: 60%;
   padding-bottom: 1.2rem;
   margin: 0 auto 2rem auto;
   border-bottom: 1px dashed #61DAF7;
   font-size: 2.5rem;
   letter-spacing: .2rem;
}

.searchedTasks__icon {
   font-size: 2.5rem;
   cursor: pointer;
   transition: .2s;
}

.searchedTasks__icon:hover {
   color: #61DAF7;
   -webkit-transform: scale(1.2);
           transform: scale(1.2)
}

@media (max-width: 750px) {
   .searchedTasks__title {
      font-size: 3rem;
   }

   .searchedTasks__item {
      width: 80%;
   }
}


@media (max-width: 600px) {
   .searchedTasks__title {
      font-size: 2.5rem;
   }

   .searchedTasks__item {
      width: 90%;
   }
}
.tasksToDo {
   min-height: 25rem;
   margin-bottom: 3.5rem;
   border-top: 1px solid #61DAF7;
}

.tasksToDo__title {
   margin: 1.5rem auto;
   font-size: 3.5rem;
}

.tasksToDo__list {
   padding: 0;
   list-style: none;
}

.tasksToDo__item {
   width: 80%;
   padding-bottom: 1.2rem;
   margin: 0 auto 2rem auto;
   border-bottom: 1px dashed #61DAF7;
   font-size: 2.5rem;
   letter-spacing: .2rem;
   transition: .2s;
}

.tasksToDo__item:hover {
   color: #61DAF7;
}

.tasksToDo__icon {
   margin-left: 5rem;
   cursor: pointer;
   transition: .2s;
}

.tasksToDo__icon:hover {
   -webkit-transform: scale(1.2);
           transform: scale(1.2)
}

.taskToDo__input {
   height: 4rem;
   padding: 0 1.5rem;
   margin-bottom: 2.5rem;
   background: #DDD;
   font-size: 1.6rem;

}

@media (max-width: 750px) {
   .tasksToDo__title {
      font-size: 3rem;
   }

   .tasksToDo__item {
      width: 80%;
   }
}

@media (max-width: 600px) {
   .tasksToDo__title {
      font-size: 2.5rem;
   }

   .tasksToDo__item {
      width: 95%;
   }

   .tasksToDo__icon {
      margin-left: 2.9rem;
   }

}
